.FormSectionTitle {
  font-size: 14pt;
  border-bottom: 1px solid #ccc;
  padding-top: 16px;
  padding-bottom: 8px;
  margin-bottom: 16px; }

.FormHelp {
  font-style: italic;
  color: #666; }

.InlineFormFeedback {
  padding: 10px;
  background: #fff6cd;
  border: 1px solid #ffd204;
  margin-bottom: 16px; }

.LayoutSelector {
  display: flex;
  width: 100%;
  margin-bottom: 30px;
  cursor: pointer; }
  .LayoutSelector .LayoutOption {
    padding: 20px;
    border-right: 1px solid #ccc;
    width: 100%; }
  .LayoutSelector .LayoutOption:last-child {
    border-right: 0; }
  .LayoutSelector .help {
    font-family: "Roboto", sans-serif;
    font-size: 10pt;
    margin-top: 10px; }

.ValidationMessage {
  font-family: "Roboto", sans-serif;
  padding: 32px;
  background: #eaccbb; }

.ValidationMessage li {
  list-style-type: none; }

.Preview {
  transition: padding .5s; }
  .Preview section:first-child {
    margin-top: 0; }
  .Preview .PreviewContainer {
    position: relative; }
  .Preview.FloatingPreview {
    position: fixed;
    right: 32px;
    bottom: 0;
    width: 500px;
    background: white;
    padding: 16px;
    box-shadow: 0 0 10px #66cc00;
    border-radius: 16px 16px 0 0;
    z-index: 9999999;
    max-height: 450px; }
    .Preview.FloatingPreview .PreviewContainer {
      transform: scale(0.5) translateX(-50%) translateY(-50%);
      width: 200%; }
    .Preview.FloatingPreview .PreviewBar {
      font-size: 9pt;
      padding: 8px; }
  .Preview .PreviewBar {
    border-bottom: 1px solid #8bcc49;
    font-style: italic;
    padding: 12px; }
    .Preview .PreviewBar .DetachPreview {
      cursor: pointer;
      float: right; }
  .Preview .submenu-sidebar {
    float: unset !important; }
